<template>
  <v-container >
    <v-form ref="form">
      <v-row>
        <v-col cols="4" offset="4">
          <v-card-title style="justify-content: center"> </v-card-title>
        </v-col>
      </v-row>

      <v-row>
        <v-col cols="6" offset="3">
          <v-divider></v-divider>
        </v-col>
      </v-row>

      <v-row>
        <v-col cols="4" offset="4">
          <v-text-field v-model="info.id" label="id"></v-text-field>
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="4" offset="4">
          <v-text-field v-model="info.nick" label="nickname"></v-text-field>
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="4" offset="4">
          <v-text-field v-model="info.pw" label="password"></v-text-field>
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="4" offset="4">
          <v-text-field v-model="info.pwcheck" label="check password"></v-text-field>
        </v-col>
      </v-row>
      <v-row justify="center">
        <v-col cols="auto">
          <v-btn to="/login"></v-btn>
        </v-col>
        <v-col cols="2">
          <v-btn @click="signup" style="width: 100%"></v-btn>
        </v-col>
      </v-row>

      <v-row>
        <v-col cols="6" offset="3">
          <v-divider></v-divider>
        </v-col>
      </v-row>
    </v-form>

    <!--   -->
    <v-dialog
      max-width="300"
      v-model="popupDialog"
    >
      <popup-dialog
        :headerTitle=title
        btn1Title=""
        :btn2=false
        @hide="hideDialog"
        @submit="checkDialog"
      >
        <template v-slot:body>
          <!--    -->
          <div> {{text}} .</div>
        </template>
      </popup-dialog>
    </v-dialog>
    <!--   -->

  </v-container>
</template>

<script>
import herokuAPI from '@/api/heroku.js';
import router from '@/router/index.js';
import PopupDialog from '@/components/popup.vue';

export default{
  components: {
    PopupDialog
  },
  data() {
    return {
      popupDialog: false,
      title: "",
      text: "",
      info: {
        email: null,
        id: null,
        nick: null,
        pw: null,
        pwcheck: null,
      }
    }
  },
  created() {
    this.info.email = localStorage.getItem("email");
    console.log("  ", this.info.email);
    localStorage.removeItem("email");
  },
  methods: {
    showDialog(){ //  
      this.popupDialog = true
    },
    hideDialog(){ //  
      this.popupDialog = false
    },
    checkDialog(){ //   
      //     
      this.hideDialog();
    },
    signup() {
      let vm = this;
      const signupInfo = JSON.stringify({
        "nickname": this.info.nick,
        "uid": this.info.id,
        "password": this.info.pw,
        "email": this.info.email,
        "auto_login": false,
      });
      console.log(signupInfo);
      herokuAPI.signup(signupInfo)
        .then(function (response) {
          console.log(response)
          if(response.status == 200) {
            console.log(" ")
            router.push({name: 'login'});
          }
        }) 
        .catch(function (e) {
          if(e.response.status == 400) {
            console.log("400 error");
            vm.text = "";
            vm.title = " ";
            vm.showDialog();
          } else if(e.response.status == 401) {
            console.log("401 error");
            vm.text = "";
            vm.title = " ";
            vm.showDialog();
          } else if(e.response.status == 402) {
            console.log("402 error");
            vm.text = ", ";
            vm.title = ",  ";
            vm.showDialog();
          }
        });
    },
  }
}
</script>