<template>
  <v-container>
        
    <v-btn @click="sendMail" style="width: 100%"> </v-btn>
  </v-container>
</template>

<script>
import herokuAPI from '@/api/heroku.js';

export default{
  data(){
    return {
      sendedMail: null,
    }
  },
  methods: {
    sendMail() {
      let vm = this;
      const UserInfo = JSON.parse(localStorage.getItem("UserInfo"));
      const newMail = JSON.stringify (
        {
          "mail_id" : 0,
          "nickname" : UserInfo.nickname,
          "receiver" : "bye",
          "title" : "web test",
          "contents" : "web test",
          "send_time" : "",
          "sender_check" : 0,
          "receiver_check" : 0
        }
      );
      herokuAPI.mailSend(newMail) 
        .then(function (response) {
          console.log(" ",  newMail);
          if(response.status == 200) {
            console.log(" ", response.data);
          }
        })
    }
  }
}

</script>