<template>
  <v-card>
    <!--   -->
    <v-card-title class="justify-start">
      
    </v-card-title>

    <v-card-text>
      <v-textarea
        label="   "
        v-model="mailContents"
        required
        outlined
      ></v-textarea>
    </v-card-text>

    <!--   -->
    <v-card-actions class="justify-center mr-2 pb-4">
      <v-btn 
        color="#7895B2"
        small
        @click="$emit('hide')"
      >
        
      </v-btn>
      <template>
        <v-btn
          color="#7895B2"
          small
          @click="reportPopup"
        >
          
        </v-btn>
      </template>
    </v-card-actions>

    <!--   -->
    <v-dialog
      max-width="300"
      v-model="popupDialog"
    >
      <popup-dialog
        :headerTitle=titlePopup
        :btn1Title=titleBtn1
        :btn2Title=titleBtn2
        :btn2=btn2
        @hide="hideDialog"
        @submit="checkDialog"
      >
        <!--    -->
        {{ content }}
      </popup-dialog>
    </v-dialog>
    <!--   -->

  </v-card>
</template>

<script>
import PopupDialog from '@/components/popup.vue';
import herokuAPI from '@/api/heroku.js';

export default{
  name: "CreateMail",
  components: {
    PopupDialog
  },
  data() {
    return {
      popupDialog: false,
      mailContents: "",
      titlePopup: "",
      content: "",
      titleBtn1: "",
      titleBtn2: "",
      btn2: true,
    };
  },
  props: {
    postType: {
      type: Number
    },
    postID: {
      type: Number
    },
  },
  methods: {
    showDialog(){ //  
      this.popupDialog = true
    },
    hideDialog(){ //  
      this.popupDialog = false
    },
    checkDialog(){ //   
      //     
      this.reportPost();
      this.hideDialog();
    },
    reportFailPopup(){
      this.titlePopup = "  ";
      this.content = "  .";
      this.titleBtn1 = "";
      this.btn2 = false;
      this.showDialog();
    },
    reportPopup(){
      this.titlePopup = " ";
      this.content = " ?";
      this.titleBtn1 = "";
      this.titleBtn2 = "";
      this.btn2 = true;
      this.showDialog();
    },
    emitMethod(){
      this.$emit('hide');
    },
    reportPost(){ //    
      let vm = this;
      console.log("  "); // ->1, ->-1, ->2
      const UserInfo = JSON.parse(localStorage.getItem("UserInfo"));
      const reportInfo = JSON.stringify ({
        "id": 0,
        "contents": vm.mailContents,
        "post_type": vm.postType,
        "post_id": vm.postID,
        "reporter": UserInfo.nickname
      });
      console.log("  ", reportInfo);
      if(vm.postType == 1) {
        herokuAPI.recipeReport(reportInfo)
        .then(function (response) {
          if(response.status == 200) {
            console.log("   ");
            vm.emitMethod();
          }
        })
        .catch(function (e) {
          if(e.response.status == 500) {
            console.log("500 DB error");
            vm.reportFailPopup();
          } else if(e.response.status == 502) {
            console.log("502 Unknown error");
            vm.reportFailPopup();
          }
        });
      } else if(vm.postType == 2) {
        herokuAPI.photoReport(reportInfo)
        .then(function (response) {
          if(response.status == 200) {
            console.log("   ");
            vm.emitMethod();
          }
        })
        .catch(function (e) {
          if(e.response.status == 500) {
            console.log("500 DB error");
            vm.reportFailPopup();
          } else if(e.response.status == 502) {
            console.log("502 Unknown error");
            vm.reportFailPopup();
          }
        });
      } else if(vm.postType == -1) {
        herokuAPI.commentReport(reportInfo)
        .then(function (response) {
          console.log(" ", response);
          if(response.status == 200) {
            console.log("  ");
            vm.emitMethod();
          }
        })
        .catch(function (e) {
          if(e.response.status == 404) {
            console.log("404 DB error");
            vm.reportFailPopup();
          } else if(e.response.status == 500) {
            console.log("500 Unknown error");
            vm.reportFailPopup();
          }
        });
      }
    },
  }
};
</script>